import axios from 'axios';

const BASE_URL = 'https://redis-guideops-chat-production.up.railway.app';
const url = x => `${BASE_URL}${x}`;

console.log('[API v2 TEST] Redis Streams testing API (no auth)');

/**
 * Test Redis Streams by directly calling backend with hardcoded user
 */
export const testRedisStreams = async () => {
  try {
    console.log('[TEST] Testing Redis Streams functionality...');
    
    // Test system status
    const status = await axios.get(url('/v2/system/status'));
    console.log('[TEST] System status:', status.data);
    
    // Test clear room (requires manual call for now)
    console.log('[TEST] Manual Redis Streams test completed');
    
    return true;
  } catch (error) {
    console.error('[TEST] Redis Streams test failed:', error);
    return false;
  }
};

