/**
 * Simulate sending a message with perfect attribution
 */
export const sendTestMessage = (roomId, messageText, currentUser) => {
  return new Promise(resolve => {
    const now = Date.now();
    const streamId = `${now}-0`;
    
    const newMessage = {
      id: streamId,
      roomId: roomId,
      from: currentUser.id,
      user: {
        id: currentUser.id,
        username: currentUser.username,
        first_name: currentUser.first_name,
        last_name: currentUser.last_name,
        role: currentUser.role
      },
      text: messageText,
      message: messageText,
      tsServer: now,
      date: Math.floor(now / 1000),
      kind: "message"
    };
    
    console.log(`[TEST] Simulated message sent: ${streamId} from ${currentUser.username}`);
    
    setTimeout(() => resolve(newMessage), 200);
  });
};
